
@import "../mixins/breakpoints";

*:root {
    --font-family: "Roboto", sans-serif;
    --second-family: "TT Firs Neue", sans-serif;
    --third-family: "Muller", sans-serif;
  }


*,
*::before,
*::after {
  box-sizing: border-box;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

h2,
h3,
h4 {
  font-family: 'TTFirsNeue', sans-serif;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn-reset {
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1320px;

  @include big-desctop {
    padding: 0 30px;
  }

  @include tablet {
    padding: 0 27px;
  }

  @include mobile {
    padding: 0 21px;
  }
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;

  @include big-desctop {
    grid-template-columns: repeat(8, 1fr);
  }

  @include tablet {
    grid-template-columns: repeat(6, 1fr);

  }

  @include mobile {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;

  }
}
